<template>
	<view class="my-search-container" :style="{ 'background-color':bgcolor}" 
	@click="searchBoxHandler">
		<view class="my-search-box" :style="{'border-radius':radius+'px'}">
			<uni-icons type="search" size="17"></uni-icons>
			<text class="placeholder">搜索</text>
		</view>

	</view>
</template>

<script>
	export default {
		props: {
			// 背景颜色
			bgcolor: {
				type: String,
				default: '#C00000'
			},
			// 圆角尺寸
			radius: {
				type: Number,
				default: 18 // px
			}
		},
		data() {
			return {

			};
		},
		methods:{
			searchBoxHandler(){
				this.$emit('myclick')
			}
		}
	}
</script>

<style lang="scss">
	.my-search-container {
		height: 50px;
		display: flex;
		align-items: center;
		padding: 0 10px;
		background-color: #C00000;

		.my-search-box {
			height: 36px;
			background-color: #FFF;
			border-radius: 18px;
			width: 100%;
			display: flex;
			justify-content: center;
			align-items: center;

			.placeholder {
				font-size: 12px;
				margin-left: 5px;
			}
		}
	}
</style>
